import { Pipe, PipeTransform } from '@angular/core';
import { TreeNode } from './types/treenode';

@Pipe({name: 'disablenode'})
export class DisableNodePipe implements PipeTransform {
    transform(node: TreeNode, ...args: any[]): any {
        const selectable = !node.selectable;
        let selectable2 = false;
        if (args && args.length) {
            if (args[0]) {
                selectable2 = args[0]({node});
            }

            if (args[1]) {  // 
                return true;
            }
        }

        return selectable || selectable2;
    }
}